import React from 'react';
import { TrashIcon, PenIcon } from '../../assets/images/index';
import { InputBig , InputTextArea, } from '.';
import { AnimatePresence,motion } from 'framer-motion';

interface Props {
    id: number;
}


const InputWindowStep:React.FC<Props> = props => {
    const pageTransition = {
        in: {
          opacity: 1,
          x: 0,
          transition: {
            duration: 0.8
          }
        },
        out: {
          x: 20,
          opacity: 0,
        },
    }
    const isError = [
        {isEmpty1: false},
        {isEmpty2: false},
        {isEmpty3: false}
    ];
    const calendarObj = [
        {value: '1'},
        {value: '2'},
        {value: '3'},
        {value: '4'},
        {value: '5'},
        {value: '6'},
        {value: '7'},
        {value: '8'},
        {value: '9'},
        {value: '10'},
        {value: '11'},
        {value: '12'},
    ];

    const deleteHandler = () => {
        const article = document.getElementById(`inputWindow-${props.id}`)!;
        if(props.id !== 1) {
            article.style.display = 'none';
        }
    }

    return(
        <>
        <motion.article id={`inputWindow-${props.id}`} key={props.id} className="contentBox contentBox--big entry" initial="out" animate="in" exit="out" variants={pageTransition}>
            {props.id === 1 && <h1 className="heading4"></h1>}
            <InputBig className="mb42" type="string" labelTxt="" isRequired={true} isRequiredTxt={true} placeholderTxt=") " isError={isError} isIcon={false} />
            <InputTextArea ttl="" placeholder=""/>
            {
                props.id !== 1 && <div className="btn btn--delete" onClick={deleteHandler}><img src={TrashIcon} alt=""/></div>
            }
            
        </motion.article>
        </>
    );
};

export default InputWindowStep;